@import "vars";
@import "fonts";
// @import "libs"
@import "libs/ion.rangeSlider.scss";
@import "libs/ion.rangeSlider.skinModern.scss";

::placeholder {
	color: #666; }

::selection {
	background-color: $accent;
	color: #fff; }

body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	line-height: 1.65;
	font-family: $default-font;
	overflow-x: hidden;
	color: $text;
	// background-color: #eaeaea
	background-color: green;

	input, textarea {
		border: #666 1px solid;
		outline: none;
		&:focus:required:invalid {
			border-color: red; }
		&:required:valid {
			border-color: green; } } }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
	&.img-width {
		width: 100%; } }

.text-center {
	text-align: center; }

b, strong {
	font-weight: bold; }



@import "media"; // Always at the end

.title {
	@include r(1024) {
		color: red; } }
